.product-main {
  padding: 20px 0 50px;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__desc {
    width: calc(100% - 755px);
  }
  &__article {
    font-size: 16px;
    line-height: 120%;
    color: #ADADAD;
    margin-top: 20px;
    span {
      color: #242424;
      margin-left: 15px;
    }
  }
  &__color {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  &__color-title {
    font-size: 16px;
    line-height: 120%;
    color: #ADADAD;
    margin-bottom: 10px;
  }
  &__color-items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-left: 15px;
  }
  &__color-item {
    position: relative;
    margin-bottom: 10px;
    input {
      position: absolute;
      z-index: 1;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      opacity: 1;
      border-color: #242424;
    }
    label {
      position: relative;
      height: 32px;
      font-size: 16px;
      line-height: 120%;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid transparent;
      background: #F0F0F0;
      border-radius: 40px;
      padding: 5px 12px 5px 40px;
      opacity: 0.8;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 12px;
        transform: translate(0, -50%);
        width: 24px;
        height: 24px;
        border: 1px solid #ffffff;
        border-radius: 50%;
      }
    }
    .blue {
      &:after {
        background: #93C7E8;
      }
    }
    .pink {
      &:after {
        background: #EBC9CF;
      }
    }
  }
  &__color-item:not(:last-child) {
    margin-right: 10px;
  }
  &__text {
    max-width: 400px;
    font-size: 16px;
    line-height: 150%;
    margin-top: 20px;
  }
  &__link {
    display: flex;
    align-items: center;
    margin-top: 20px;
    span {
      font-size: 16px;
      line-height: 120%;
      color: #0A9E00;
    }
    i {
      flex-shrink: 0;
      width: 6px;
      margin-left: 12px;
      svg {
        width: 100%;
        height: auto;
      }
    }
  }
  &__group {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #F0F0F0;
    border-bottom: 1px solid #F0F0F0;
    padding: 20px 0 0 0;
    margin-top: 80px;
  }
  &__price {
    margin-right: 15px;
    margin-bottom: 20px;
    span:nth-child(1) {
      font-size: 32px;
      line-height: 120%;
      font-weight: 700;
    }
    span:nth-child(2) {
      font-size: 32px;
      line-height: 120%;
      font-weight: 500;
      text-decoration: line-through;
      color: #808080;
      margin-left: 15px;
    }
  }
  &__btns {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  .ccalc-btns {
    height: 44px;
    display: flex;
    align-items: center;
    background: #F0F0F0;
    border-radius: 50px;
    padding: 10px 12px;
    input {
      width: 48px;
      height: 24px;
      font-size: 16px;
      line-height: 105%;
      font-weight: 500;
      text-align: center;
      background: transparent;
      border: none;
      padding: 0;
      margin: 0;
    }
  }
  .ccalc-plus {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    line-height: 105%;
    font-weight: 500;
    cursor: pointer;
  }
  .ccalc-minus {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    line-height: 105%;
    font-weight: 500;
    cursor: pointer;
  }
  &__btn {
    width: 150px;
    height: 44px;
    margin-left: 20px;
  }
}
.gallery {
  position: relative;
  width: 725px;
  height: 560px;
  display: flex;
  align-items: center;
  margin-right: 30px;
  &__small-wrapp {
    width: 145px;
    height: 100%;
    margin-right: 30px;
  }
  &__small {
    height: 100%;
  }
  &__big {
    width: calc(100% - 175px);
    height: 100%;
  }
  .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  .swiper-pagination {
    bottom: -35px;
    display: none;
  }
}
.product-info {
  padding: 50px 0;
  &__tabs-head {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  &__tabs-body {
    margin-top: 30px;
  }
  &__tab {
    min-width: 190px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: #ffffff;
    font-size: 16px;
    line-height: 120%;
    text-transform: uppercase;
    border: 1px solid #242424;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s;
    padding: 3px 15px;
    margin-right: 20px;
    margin-bottom: 20px;
    &:hover {
      background: #84d904;
      border-color: #84d904;
      color: #ffffff;
    }
  }
  .tab--active {
    background: #84d904;
    border-color: #84d904;
    color: #ffffff;
  }
  .tabs-content {
    display: none;
  }
  .tabs-content--active {
    display: block;
  }
  &__slider {
    position: relative;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  @media screen and (min-width: 541px) {
    &__swiper-wrapper {
      transform: unset !important;
      display: grid;
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
  &__item {
    height: auto;
    display: flex;
    align-items: center;
    background: #F0F0F0;
    border-radius: 40px;
    padding: 60px 100px;
    &-img {
      width: 550px;
      margin-right: 70px;
      img {
        width: 100%;
      }
    }
    &-desc {
      width: calc(100% - 650px);
    }
    &-title {
      font-weight: 700;
      text-decoration: underline;
      text-underline-offset: 9px;
      text-decoration-color: #84d904;
    }
    &-text {
      max-width: 395px;
      font-size: 16px;
      line-height: 150%;
      margin-top: 18px;
    }
  }
  &__item2 {
    .product-info__item-img {
      order: 1;
      margin: 0 0 0 70px;
    }
  }
  .swiper-pagination {
    bottom: -35px;
    display: none;
  }
  &__nav {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 60px / 2));
    gap: 60px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 17px;
    li {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 9px;
        width: 100%;
        height: 1px;
        border: 1px dashed #ADADAD;
      }
    }
    li span {
      position: relative;
      z-index: 1;
      font-size: 16px;
      line-height: 120%;
      background: #ffffff;
      padding: 3px 0;
    }
    li span:nth-child(1) {
      color: #ADADAD;
      padding-right: 10px;
    }
    li span:nth-child(2) {
      padding-left: 10px;
      margin-left: 15px;
    }
  }
  &__video-group {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
  }
  &__video {
    height: 335px;
    iframe {
      width: 100%;
      height: 100%;
    }
  }
}

@media screen and (max-width: 1400px) {
  .product-main {
    &__desc {
      width: calc(100% - 535px);
    }
    &__price {
      span:nth-child(2) {
        font-size: 24px;
      }
    }
    &__group {
      margin-top: 25px;
    }
  }
  .gallery {
    width: 535px;
    height: 440px;
    &__small-wrapp {
      width: 65px;
      .swiper-slide {
        height: 65px !important;
      }
    }
    &__big {
      width: calc(100% - 95px);
    }
  }
  .product-info {
    &__item {
      padding: 50px;
      &-img {
        width: 420px;
        margin-right: 30px;
      }
      &-desc {
        width: calc(100% - 450px);
      }
      &-title {
        font-size: 32px;
      }
    }
    &__item2 {
      .product-info__item-img {
        margin: 0 0 0 30px;
      }
    }
    &__video {
      height: 250px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .product-main {
    padding: 0 0 40px 0;
    &__title {
      font-size: 32px;
    }
  }
  .product-info {
    padding: 40px 0;
    &__video {
      height: 225px;
    }
  }
}

@media screen and (max-width: 960px) {
  .product-main {
    &__desc {
      width: calc(100% - 290px);
    }
  }
  .gallery {
    width: 260px;
    height: 220px;
    &__small-wrapp {
      width: 50px;
      margin-right: 10px;
      .swiper-slide {
        height: 50px !important;
      }
    }
    &__big {
      width: calc(100% - 60px);
    }
  }
  .product-info {
    &__tabs-body {
      margin-top: 10px;
    }
    &__tab {
      min-width: 150px;
    }
    &__item {
      padding: 40px 20px;
      &-img {
        width: 270px;
      }
      &-desc {
        width: calc(100% - 300px);
      }
      &-title {
        font-size: 24px;
      }
    }
    &__nav {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
      gap: 30px;
    }
  }
}

@media screen and (max-width: 740px) {
  .product-main {
    &__inner {
      display: block;
    }
    &__desc {
      width: 100%;
    }
  }
  .gallery {
    width: 100%;
    height: 400px;
    margin: 0 0 50px 0;
    &__small-wrapp {
      width: 80px;
      margin-right: 30px;
      .swiper-slide {
        height: 80px !important;
      }
    }
    &__big {
      width: calc(100% - 110px);
    }
  }
  .product-info {
    &__item {
      flex-direction: column;
      padding: 30px;
      &-img {
        width: 100%;
        max-width: 270px;
        margin: 0 auto 30px;
      }
      &-desc {
        width: 100%;
      }
      &-title {
        br {
          display: none;
        }
      }
      &-text {
        max-width: 100%;
      }
    }
    &__item2 {
      .product-info__item-img {
        margin: 30px auto 0;
      }
    }
    &__nav {
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__video-group {
      max-width: 450px;
      grid-template-columns: 100%;
      margin: 0 auto;
    }
  }
}

@media screen and (max-width: 540px) {
  .gallery {
    max-width: 300px;
    height: 300px;
    margin: 0 auto 50px;
    &__small-wrapp {
      display: none;
    }
    &__big {
      width: 100%;
    }
    .swiper-pagination {
      display: block;
    }
  }
  .product-info {
    &__tab {
      width: 100%;
    }
    .swiper-pagination {
      display: block;
    }
    &__video-group {
      max-width: 300px;
      gap: 20px;
    }
    &__video {
      height: 150px;
    }
  }
}