.catalog-main {
  padding-bottom: 100px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #84D904;
    padding-bottom: 10px;
  }
  &__filter-btn {
    width: 150px;
    height: 38px;
    display: none;
    justify-content: center;
    align-items: center;
    background: #ADADAD;
    border-radius: 50px;
    padding: 3px;
    cursor: pointer;
    margin-left: 15px;
    i {
      flex-shrink: 0;
      width: 18px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
      margin-left: 10px;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }
  &__content {
    width: calc(100% - 380px);
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 60px / 3));
    gap: 30px;
  }
  .pagination {
    margin-top: 40px;
  }
  &__text {
    background: #F0F0F0;
    border-radius: 40px;
    padding: 50px;
    margin-top: 50px;
    p {
      max-width: 910px;
      font-size: 16px;
      line-height: 150%;
    }
    p:not(:first-child) {
      margin-top: 20px;
    }
  }
}
.catalog-filter {
  width: 350px;
  margin-right: 30px;
  &__bg {
    position: fixed;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.25);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &--active {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__group {
    &:not(:last-child) {
      margin-bottom: 50px;
    }
  }
  &__title {
    font-size: 28px;
    line-height: 120%;
    font-weight: 500;
    margin-bottom: 16px;
  }
  &__checkbox {
    position: relative;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #F0F0F0;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 12px 9px;
        background-color: #84d904;
      }
    }
    label {
      position: relative;
      width: 100%;
      font-size: 16px;
      line-height: 120%;
      padding: 12px 0 12px 36px;
      &:after {
        content: '';
        position: absolute;
        top: 9px;
        left: 0;
        width: 24px;
        height: 24px;
        background: #F0F0F0;
        border-radius: 8px;
        transition: all 0.3s;
      }
    }
  }
  &__btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__btn {
    width: 50%;
    height: 44px;
  }
  &__btn2 {
    width: 50%;
    height: 44px;
    background: #F0F0F0;
    border-radius: 50px;
    margin-left: 12px;
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      color: #ADADAD;
    }
    &:hover {
      background: #bbbbbb;
      span {
        color: #ffffff;
      }
    }
  }
  .noUi-horizontal {
    height: 3px;
    border: none;
    box-shadow: none;
    border-radius: 2px;
  }
  .noUi-connects {
    background: #F0F0F0;
  }
  .noUi-connect {
    background: #84D904;
  }
  .noUi-handle {
    top: -5px;
    right: -8px;
    width: 12px;
    height: 12px;
    background: #84d904;
    border: none;
    box-shadow: unset;
    border-radius: 50%;
    &:after,
    &:before {
      display: none;
    }
  }
  .range__numbers {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 8px;
    input[type="number"]::-webkit-outer-spin-button,
    input[type="number"]::-webkit-inner-spin-button {
      -webkit-appearance: none;
    }
    input[type='number'],
    input[type="number"]:hover,
    input[type="number"]:focus {
      appearance: none;
      -moz-appearance: textfield;
    }
  }
  .range__number {
    width: 52px;
    font-size: 16px;
    line-height: 120%;
    color: #ADADAD;
    background: transparent;
    border: none;
    padding: 0;
    margin: 0;
  }
  .range-text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    span {
      font-size: 16px;
      line-height: 120%;
    }
  }
}

@media screen and (max-width: 1300px) {
  .catalog-main {
    &__content {
      width: calc(100% - 280px);
    }
    .catalog-card {
      &:nth-child(9) ~ .catalog-card {
        display: none;
      }
    }
    &__text {
      padding: 40px;
    }
  }
  .catalog-filter {
    width: 250px;
  }
}

@media screen and (max-width: 1100px) {
  .catalog-main {
    &__content {
      width: calc(100% - 240px);
    }
    .catalog-card {
      &__img {
        max-width: 210px;
        height: 210px;
      }
    }
    &__text {
      margin-top: 30px;
    }
  }
  .catalog-filter {
    width: 210px;
    &__btns {
      display: block;
    }
    &__btn {
      width: 100%;
    }
    &__btn2 {
      width: 100%;
      margin: 20px 0 0 0;
    }
  }
}

@media screen and (max-width: 960px) {
  .catalog-main {
    &__inner {
      position: relative;
    }
    &__filter-btn {
      display: flex;
    }
    &__wrap {
      display: block;
    }
    &__content {
      width: 100%;
    }
  }
  .catalog-filter {
    position: absolute;
    z-index: 3;
    top: 65px;
    right: -105%;
    width: 300px;
    background: #fff;
    box-shadow: 2px -1px 29px 0px rgba(92, 92, 92, 0.18);
    border-radius: 20px;
    padding: 30px;
    margin-right: 0;
    transition: all 0.5s;
    &--active {
      right: 0;
    }
    &__title {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 740px) {
  .catalog-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
    }
    .catalog-card {
      &:nth-child(8) ~ .catalog-card {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 540px) {
  .catalog-main {
    &__cards {
      max-width: 350px;
      grid-template-columns: 100%;
      margin: 0 auto;
    }
    .catalog-card {
      &:nth-child(4) ~ .catalog-card {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 390px) {
  .catalog-main {
    &__text {
      padding: 30px 15px;
    }
  }
  .catalog-filter {
    width: 100%;
  }
}