.main {
  &__title {
    max-width: 450px;
    font-size: 32px;
    line-height: 120%;
  }
  &__text {
    max-width: 310px;
    font-size: 16px;
    line-height: 150%;
    margin-top: 8px;
  }
  &__btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 16px;
  }
  &__btn {
    width: 212px;
    height: 44px;
  }
  .swiper-pagination {
    position: static;
    width: auto;
    margin: 0 32px;
  }
  &__slider-btns {
    display: flex;
    align-items: center;
  }
  .swiper-button-prev {
    position: static;
    width: 32px;
    height: 32px;
  }
  .swiper-button-next {
    position: static;
    width: 32px;
    height: 32px;
    margin-left: 12px;
  }
}
.main-slide {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 320px;
  &__wrap {
    background: rgba(255, 255, 255, 0.6);
    padding: 50px 0;
  }
}
.catalog {
  padding: 100px 0 50px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #84D904;
    padding-bottom: 10px;
  }
  &__slider-btns {
    display: flex;
    align-items: center;
    margin-left: 32px;
  }
  .swiper-button-prev {
    position: static;
    width: 48px;
    height: 48px;
  }
  .swiper-button-next {
    position: static;
    width: 48px;
    height: 48px;
    margin-left: 15px;
  }
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-next,
    &-next + .swiper-slide,
    &-next + .swiper-slide + .swiper-slide {
      opacity: 1;
      pointer-events: all;
    }
  }
  .swiper-pagination {
    bottom: -45px;
    display: none;
  }
  &__btn {
    width: 255px;
    height: 44px;
    margin: 50px auto 0;
  }
}
.catalog-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  text-align: center;
  padding: 20px 0;
  transition: all 0.3s;
  &:after {
    content: '';
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(224deg, rgba(64, 64, 64, 0.50) -5.13%, #242424 100%);
    border-radius: 20px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    transition-delay: 2s;
  }
  &:hover {
    background: #ffffff;
    box-shadow: 2px -1px 29px 0px rgba(92, 92, 92, 0.18);
    border-radius: 20px;
    &:after {
      opacity: 0.5;
      pointer-events: all;
    }
    .catalog-card__btn-opacity {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__img {
    height: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 10px;
    margin-top: 24px;
  }
  &__name {
    font-size: 12px;
    line-height: 120%;
    color: #808080;
  }
  &__title {
    max-width: 300px;
    font-size: 18px;
    line-height: 120%;
    margin: 10px auto auto;
  }
  &__price {
    font-size: 28px;
    line-height: 120%;
    margin-top: 20px;
  }
  &__btn {
    width: 150px;
    height: 44px;
    margin: 20px auto 0;
  }
  &__btn-opacity {
    position: absolute;
    z-index: 3;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 150px;
    height: 44px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    transition-delay: 2s;
  }
}
.info {
  padding: 50px 0;
  &__wrap {
    display: flex;
    align-items: center;
  }
  &__img {
    flex-shrink: 0;
    width: 625px;
    margin-right: 90px;
    img {
      width: 100%;
    }
  }
  &__desc {
    max-width: 410px;
  }
  &__subtitle {
    font-size: 12px;
    line-height: 120%;
    text-transform: uppercase;
  }
  &__title {
    text-decoration: underline;
    text-decoration-color: #84d904;
    text-underline-offset: 9px;
    margin-top: 12px;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    margin-top: 24px;
  }
  &__btn {
    width: 212px;
    height: 44px;
    margin-top: 20px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 90px / 4));
    gap: 30px;
    margin-top: 100px;
  }
  &__item {
    max-width: 190px;
    text-align: center;
    &-img {
      max-width: 150px;
      height: 150px;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      margin: 0 auto;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 500;
      margin-top: 40px;
    }
  }
}
.news {
  padding: 50px 0 100px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #84D904;
    padding-bottom: 10px;
  }
  &__slider-btns {
    display: flex;
    align-items: center;
    margin-left: 32px;
  }
  .swiper-button-prev {
    position: static;
    width: 48px;
    height: 48px;
  }
  .swiper-button-next {
    position: static;
    width: 48px;
    height: 48px;
    margin-left: 15px;
  }
  &__slider {
    position: relative;
    margin-top: 50px;
  }
  .swiper-pagination {
    bottom: -45px;
    display: none;
  }
  &__btn {
    width: 255px;
    height: 44px;
    margin: 50px auto 0;
  }
}
.news-card {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(224deg, rgba(64, 64, 64, 0.50) -5.13%, #242424 100%);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    &:after {
      opacity: 0.8;
      pointer-events: all;
    }
    .news-card__btn {
      opacity: 1;
      pointer-events: all;
    }
  }
  &__img {
    position: relative;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    position: absolute;
    left: 20px;
    right: 20px;
    bottom: 20px;
    background: rgba(255, 255, 255, 0.95);
    border-radius: 20px;
    padding: 20px;
  }
  &__date {
    font-size: 12px;
    line-height: 120%;
    text-transform: uppercase;
    color: #808080;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    margin-top: 12px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
  }
  &__btn {
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 150px;
    height: 44px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
  }
}

@media screen and (max-width: 1300px) {
  .main-slide {
    &:nth-child(1) {
      background-image: url("../img/main1200-bg.webp") !important;
    }
  }
  .catalog {
    .swiper-slide {
      &-next + .swiper-slide + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}

@media screen and (max-width: 1250px) {
  .catalog {
    &__slider {
      margin-top: 30px;
    }
    &__btn {
      margin-top: 30px;
    }
  }
  .info {
    padding: 40px 0;
    &__img {
      margin-right: 40px;
    }
    &__items {
      margin-top: 80px;
    }
  }
  .news {
    padding: 40px 0 80px;
    &__slider {
      margin-top: 30px;
    }
    &__btn {
      margin-top: 30px;
    }
  }
  .news-card {
    &__desc {
      left: 15px;
      right: 15px;
      bottom: 15px;
      padding: 15px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .main-slide {
    &:nth-child(1) {
      background-image: url("../img/main992-bg.webp") !important;
    }
  }
  .catalog {
    padding: 80px 0 40px;
  }
  .catalog-card {
    &__img {
      height: 290px;
    }
  }
  .info {
    &__img {
      width: 450px;
    }
    &__item {
      &-img {
        max-width: 110px;
        height: 110px;
      }
      &-title {
        margin-top: 20px;
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .catalog-card {
    &__img {
      height: 210px;
    }
    &__desc {
      margin-top: 20px;
    }
  }
  .info {
    &__img {
      width: 330px;
      margin-right: 30px;
    }
    &__items {
      margin-top: 60px;
    }
    &__item {
      &-title {
        font-size: 16px;
      }
    }
  }
  .news {
    &__slider-btns {
      display: none;
    }
    .swiper-pagination {
      display: block;
    }
    &__btn {
      margin-top: 80px;
    }
  }
}

@media screen and (max-width: 740px) {
  .main {
    &__btns {
      flex-direction: column;
      margin-top: 10px;
    }
    .swiper-pagination {
      margin: 20px auto 0;
    }
    &__slider-btns {
      display: none;
    }
  }
  .main-slide {
    &__wrap {
      background: rgba(255,255,255,.8);
      padding: 30px 0;
      .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
      }
    }
    &__title {
      font-size: 24px;
      margin: 0 auto;
    }
    &__text {
      margin: 10px auto 0;
    }
  }
  .catalog {
    .swiper-slide {
      &-next + .swiper-slide {
        opacity: 0;
        pointer-events: none;
      }
    }
  }
  .info {
    &__wrap {
      flex-direction: column;
    }
    &__img {
      width: 100%;
      margin: 0 0 30px 0;
    }
    &__desc {
      max-width: 100%;
    }
    &__items {
      margin-top: 50px;
    }
    &__item {
      &-img {
        max-width: 64px;
        height: 64px;
      }
    }
  }
}

@media screen and (max-width: 540px) {
  .main-slide {
    padding-top: 450px;
    &:nth-child(1) {
      background-image: url("../img/main400-bg.webp") !important;
    }
  }
  .catalog {
    &__slider-btns {
      display: none;
    }
    .swiper-pagination {
      display: block;
    }
    &__slider {
      max-width: 350px;
      margin: 30px auto 0;
    }
    .swiper-slide {
      &-next {
        opacity: 0;
        pointer-events: none;
      }
    }
    &__btn {
      margin-top: 80px;
    }
  }
  .info {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 30px / 2));
    }
  }
}