.wrap {
  overflow: hidden;
}
.display1 {
  font-size: 56px;
  line-height: 120%;
}
.display2 {
  font-size: 44px;
  line-height: 120%;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-black {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #242424;
  border-radius: 50px;
  span {
    font-size: 16px;
    line-height: 120%;
    text-transform: uppercase;
    color: #ffffff;
  }
  &:hover {
    background: #84D904;
  }
}
.btn-green {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #84D904;
  border-radius: 50px;
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    text-transform: uppercase;
    color: #ffffff;
  }
  &:hover {
    background: #242424;
  }
}
.btn-black-outline {
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: 1px solid #242424;
  border-radius: 50px;
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 500;
    color: #242424;
  }
  &:hover {
    background: #242424;
    span {
      color: #ffffff;
    }
  }
}
.swiper-button-prev {
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #242424;
  border-radius: 50%;
  margin-top: 0;
  &:after {
    display: none;
  }
  img {
    max-width: 24px;
  }
}
.swiper-button-next {
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #242424;
  border-radius: 50%;
  margin-top: 0;
  &:after {
    display: none;
  }
  img {
    max-width: 24px;
  }
}
.swiper-pagination {
  z-index: 1;
  &-bullet {
    width: 12px;
    height: 12px;
    background: #242424;
  }
  &-bullet-active {

  }
}
.up {
  position: fixed;
  z-index: 5;
  bottom: 100px;
  right: 20px;
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #414141;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
  img {
    transform: rotate(-90deg);
    width: 40px;
  }
  &--active {
    opacity: 1;
    pointer-events: all;
  }
}
.breadcrumbs {
  padding: 30px 0 20px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    position: relative;
    font-size: 16px;
    line-height: 120%;
    color: #808080;
    margin-bottom: 10px;
    &:hover {
      color: #84d904;
    }
    &--active {
      color: #242424;
      &:hover {
        color: #242424;
      }
    }
    &:not(:last-child) {
      margin-right: 50px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -30px;
        transform: translate(0, -50%);
        width: 6px;
        height: 10px;
        background-image: url("../img/arrow2.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        pointer-events: none;
      }
    }
  }
}
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  &__item {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    font-size: 16px;
    line-height: 105%;
    color: #ADADAD;
    margin-left: 5px;
    &:hover {
      color: #84d904;
    }
    &--active {
      background: #84d904;
      color: #ffffff;
    }
  }
  &__btn-prev {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
      width: 6px;
      height: auto;
    }
  }
  &__btn-next {
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5px;
    svg {
      width: 6px;
      height: auto;
    }
  }
}
.nav {
  display: grid;
  grid-template-columns: 100%;
  gap: 20px;
  padding: 50px 0;
  &__link {
    font-size: 24px;
    line-height: 120%;
    &:hover {
      color: #84d904;
    }
  }
}

@media screen and (max-width: 1300px) {
  .up {
    width: 60px;
    height: 60px;
    img {
      width: 25px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .breadcrumbs {
    padding: 20px 0 10px;
  }
  .display1 {
    font-size: 48px;
  }
}

@media screen and (max-width: 740px) {
  .display2 {
    font-size: 36px;
  }
  .display1 {
    font-size: 36px;
  }
  .breadcrumbs {
    &__item {
      font-size: 12px;
    }
  }
}

@media screen and (max-width: 540px) {
  .display1 {
    font-size: 24px;
  }
  .display2 {
    font-size: 24px;
  }
}