.header-top {
  background: #ffffff;
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 70px;
  }
  &__group {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-left: 100px;
    }
  }
  &__logo {
    display: block;
    width: 128px;
    img {
      width: 100%;
    }
  }
  &__phone {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 38px;
      height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #84D904;
      border-radius: 50%;
      img {
        width: 23px;
      }
    }
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      margin-left: 15px;
      transition: all 0.3s;
    }
    &:hover {
      span {
        color: #84D904;
      }
    }
  }
  &__mail {
    display: flex;
    align-items: center;
    margin-left: 100px;
    i {
      flex-shrink: 0;
      width: 38px;
      height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #84D904;
      border-radius: 50%;
      img {
        width: 21px;
      }
    }
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      margin-left: 15px;
      transition: all 0.3s;
    }
    &:hover {
      span {
        color: #84D904;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 100px;
  }
  &__social {
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #84D904;
    border-radius: 50%;
    &:not(:first-child) {
      margin-left: 20px;
    }
    img {
      max-width: 22px;
    }
  }
  &__search {
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #242424;
    border-radius: 50%;
    cursor: pointer;
    img {
      width: 22px;
    }
  }
  &__basket {
    min-width: 115px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #242424;
    border-radius: 50px;
    padding: 5px;
    cursor: pointer;
    margin-left: 20px;
    i {
      flex-shrink: 0;
      width: 20px;
      img {
        width: 100%;
      }
    }
    &-text {
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      text-transform: uppercase;
      color: #ffffff;
      margin-left: 10px;
    }
  }
}
.header-bottom {
  color: #ffffff;
  background: #242424;
  padding: 15px 0;
  &__list {
    display: flex;
    align-items: center;
    li {
      display: flex;
      align-items: center;
      transition: all 0.3s;
    }
    li:not(:last-child) {
      margin-right: 30px;
    }
    li .line {
      width: 1px;
      height: 20px;
      background: #808080;
      opacity: 0.8;
      pointer-events: none;
      margin-left: 30px;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    font-size: 20px;
    line-height: 120%;
    &:hover {
      color: #84D904;
    }
  }
  &__item {
    position: relative;
    &--active {
      .header-bottom__item-hidden {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
      .line {
        pointer-events: none;
      }
    }
    &-hidden {
      position: absolute;
      z-index: 5;
      top: 100%;
      left: -30px;
      width: 355px;
      padding-top: 15px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-title {
      display: flex;
      align-items: center;
      span {
        font-size: 20px;
        line-height: 120%;
        transition: all 0.3s;
      }
      i {
        width: 11px;
        margin-left: 8px;
      }
      i svg {
        width: 100%;
        height: auto;
      }
      i svg path {
        transition: all 0.3s;
      }
      &:hover {
        span {
          color: #84d904;
        }
        i svg path {
          fill: #84d904;
        }
      }
    }
    &-wrap {
      background: #242424;
      padding: 5px 20px;
    }
    &-link {
      display: flex;
      align-items: center;
      padding: 15px 0;
      &:not(:last-child) {
        border-bottom: 1px solid rgba(173, 173, 173, 0.8);
      }
      &:hover {
        i svg path {
          stroke: #84D904;
        }
        i svg circle {
          stroke: #84D904;
        }
        span {
          color: #84D904;
        }
      }
      i {
        flex-shrink: 0;
        width: 25px;
      }
      i svg {
        max-width: 25px;
        height: auto;
      }
      i svg path {
        transition: all 0.3s;
      }
      i svg circle {
        transition: all 0.3s;
      }
      span {
        font-size: 20px;
        line-height: 120%;
        transition: all 0.3s;
        margin-left: 12px;
      }
    }
  }
  .opacity {
    opacity: 0.5;
  }
  &__group {
    display: none;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
  }
  .header-top__phone {
    span {
      display: none;
    }
  }
  .header-top__mail {
    margin-left: 20px;
    span {
      display: none;
    }
  }
  .header-top__socials {
    margin-left: 20px;
  }
}
.burger {
  position: relative;
  z-index: 12;
  width: 38px;
  height: 38px;
  display: none;
  justify-content: center;
  align-items: center;
  background: #242424;
  border-radius: 50%;
  cursor: pointer;
  margin-left: 20px;
  &--active {
    background: #ffffff;
    .burger__wrap {
      span {
        background: #242424;
      }
      span:nth-child(1) {
        top: 6px;
        transform: rotate(-45deg);
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        bottom: 6px;
        transform: rotate(45deg);
      }
    }
  }
  &__wrap {
    position: relative;
    width: 20px;
    height: 14px;
    span {
      position: absolute;
      width: 100%;
      height: 2px;
      background: #ffffff;
      border-radius: 3px;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
  }
}

@media screen and (max-width: 1400px) {
  .header-top {
    &__right {
      margin-left: 30px;
    }
    &__group {
      &:not(:first-child) {
        margin-left: 30px;
      }
    }
    &__mail {
      margin-left: 30px;
    }
    &__socials {
      margin-left: 30px;
    }
  }
}

@media screen and (max-width: 1100px) {
  .header-top {
    &__socials {
      display: none;
    }
  }
  .header-bottom {
    position: fixed;
    z-index: 10;
    top: 0;
    left: -105%;
    width: 100%;
    min-height: 430px;
    background: #242424;
    padding: 100px 15px 50px 15px;
    transition: all 0.5s;
    overflow-y: auto;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 130px;
      background: #393939;
      pointer-events: none;
    }
    &--active {
      left: 0;
    }
    &__inner {
      position: relative;
      z-index: 2;
    }
    &__list {
      max-width: 490px;
      flex-wrap: wrap;
      justify-content: center;
      margin: 0 auto;
      li {
        margin: 0 20px 20px 0 !important;
      }
      li .line {
        display: none;
      }
    }
    &__item {
      &-show {
        justify-content: center;
      }
      &-hidden {
        display: none;
      }
      &-title {
        i {
          display: none;
        }
      }
    }
    &__group {
      display: flex;
    }
    .header-top__socials {
      display: flex;
    }
  }
  .burger {
    display: flex;
  }
}

@media screen and (max-width: 960px) {
  .header-top {
    &__mail {
      display: none;
    }
  }
}

@media screen and (max-width: 740px) {
  .header-top {
    &__right {
      flex-grow: 0;
      margin-left: 20px;
    }
    &__group {
      &:not(:first-child) {
        margin-left: 20px;
      }
    }
    &__phone {
      span {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 540px) {
  .header-top {
    &__basket {
      position: relative;
      width: 38px;
      height: 38px;
      min-width: auto;
      border-radius: 50%;
      i {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      &-text {
        position: absolute;
        right: -5px;
        bottom: -5px;
        width: 15px;
        height: 15px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #84D904;
        border-radius: 50%;
        font-size: 10px;
        color: #242424;
        margin-left: 0;
        span:nth-child(1),
        span:nth-child(3) {
          display: none;
        }
      }
    }
  }
  .header-bottom {
    min-height: 100%;
    height: 100%;
    &:after {
      height: 55px;
    }
    &__list {
      flex-direction: column;
      li {
        margin: 0 0 20px 0 !important;
      }
    }
  }
}

@media screen and (max-width: 390px) {
  .header-top {
    &__right {
      margin-left: 15px;
    }
    &__logo {
      width: 100px;
    }
    &__group {
      &:not(:first-child) {
        margin-left: 10px;
      }
    }
    &__phone {
      i {
        width: 30px;
        height: 30px;
      }
    }
    &__search {
      width: 30px;
      height: 30px;
    }
    &__basket {
      width: 30px;
      height: 30px;
      margin-left: 10px;
    }
  }
  .burger {
    width: 30px;
    height: 30px;
    margin-left: 10px;
  }
}