.footer {
  color: #ffffff;
  background: #242424;
  padding: 50px 0;
}
.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 100px;
  }
  &__logo {
    display: block;
    width: 128px;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    align-items: center;
  }
  &__group-mob {
    display: none;
  }
  &__phone {
    display: flex;
    align-items: center;
    i {
      flex-shrink: 0;
      width: 38px;
      height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border-radius: 50%;
      img {
        width: 23px;
      }
    }
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      margin-left: 15px;
      transition: all 0.3s;
    }
    &:hover {
      span {
        color: #84D904;
      }
    }
  }
  &__mail {
    display: flex;
    align-items: center;
    margin-left: 100px;
    i {
      flex-shrink: 0;
      width: 38px;
      height: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #ffffff;
      border-radius: 50%;
      img {
        width: 21px;
      }
    }
    span {
      font-size: 20px;
      line-height: 120%;
      font-weight: 500;
      margin-left: 15px;
      transition: all 0.3s;
    }
    &:hover {
      span {
        color: #84D904;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 100px;
  }
  &__social {
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffff;
    border-radius: 50%;
    &:not(:first-child) {
      margin-left: 20px;
    }
    img {
      max-width: 22px;
    }
  }
  &__btn {
    width: 310px;
    height: 44px;
    margin-left: 100px;
    span {
      color: #242424;
    }
    &:hover {
      background: black;
      span {
        color: #ffffff;
      }
    }
  }
}
.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-left: 228px;
  margin-top: 50px;
  &__items {
    display: flex;
  }
  &__item {
    &:not(:first-child) {
      margin-left: 230px;
    }
  }
  &__group {
    position: relative;
    padding-left: 20px;
    margin-top: 20px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1px;
      height: 100%;
      background: #ffffff;
    }
  }
  &__link {
    display: block;
    font-size: 20px;
    line-height: 120%;
    &:not(:first-child) {
      margin-top: 20px;
    }
    &:hover {
      color: #84d904;
    }
  }
  &__brevis {
    min-width: 310px;
    display: flex;
    margin-left: 100px;
    &-icon {
      flex-shrink: 0;
      width: 25px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 12px;
      line-height: 120%;
      color: #808080;
      margin-left: 20px;
      a {
        text-decoration: underline;
        text-underline-offset: 3px;
      }
    }
  }
}

@media screen and (max-width: 1500px) {
  .footer-top {
    &__right {
      margin-left: 20px;
    }
    &__mail {
      margin-left: 20px;
    }
    &__socials {
      margin-left: 20px;
    }
    &__btn {
      width: 272px;
      margin-left: 20px;
    }
  }
  .footer-bottom {
    padding-left: 148px;
    &__item {
      &:not(:first-child) {
        margin-left: 150px;
      }
    }
    &__brevis {
      min-width: 272px;
      margin-left: 20px;
    }
  }
}

@media screen and (max-width: 1190px) {
  .footer-top {
    &__right {
      justify-content: flex-end;
    }
    &__phone {
      span {
        display: none;
      }
    }
    &__mail {
      span {
        display: none;
      }
    }
  }
  .footer-bottom {
    padding-left: 0;
    &__brevis {
      min-width: auto;
    }
  }
}

@media screen and (max-width: 960px) {
  .footer {
    padding: 30px 0 50px;
  }
  .footer-bottom {
    display: block;
    &__brevis {
      margin: 50px 0 0 0;
    }
  }
}

@media screen and (max-width: 740px) {
  .footer-top {
    &__group {
      display: none;
    }
    &__group-mob {
      display: flex;
      margin-top: 40px;
    }
    &__btn {
      margin: 0;
    }
  }
  .footer-bottom {
    &__items {
      justify-content: space-between;
    }
    &__item:not(:first-child) {
      margin-left: 100px;
    }
  }
}

@media screen and (max-width: 540px) {
  .footer-top {
    flex-direction: column;
    align-items: center;
    &__right {
      flex-direction: column;
      margin: 20px 0 0 0;
    }
    &__group {
      display: flex;
    }
    &__group-mob {
      display: none;
    }
    &__btn {
      margin-top: 20px;
    }
  }
  .footer-bottom {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    &__items {
      flex-direction: column;
      text-align: center;
    }
    &__item {
      &:not(:first-child) {
        margin: 20px 0 0 0;
      }
    }
    &__group {
      display: none;
    }
    &__brevis {
      margin-top: 30px;
    }
  }
}